.custom-dropdown{position:relative;width:100%}.custom-dropdown .dropdown-button{position:relative;width:624PX;padding:8px 12px;border:1px solid #ccc;background:var(--white);border-radius:4px;color:#9fabc6;font-size:14px}@media screen and (max-width: 800px){.custom-dropdown .dropdown-button{width:328px;font-size:12px}}@media screen and (max-width: 365px){.custom-dropdown .dropdown-button{width:288px}}.custom-dropdown .dropdown-button.active{border:1px solid var(--primary-color)}.custom-dropdown .dropdown-button .drop-down-toggle-btn{position:absolute;right:12px;color:#262e40;font-size:24px;cursor:pointer}.custom-dropdown .dropdown-button .drop-down-toggle-btn.active{transform:rotate(180deg)}.custom-dropdown .dropdown-button span{padding:4px 8px;background:var(--primary-variant);margin-right:8px;border-radius:4px;font-size:14px;color:var(--primary-color);font-weight:500;letter-spacing:.15%;margin-bottom:10px !important}@media screen and (max-width: 800px){.custom-dropdown .dropdown-button span{font-size:11px;margin-right:5px}}.custom-dropdown .dropdown-button span i{cursor:pointer;font-size:14px;color:var(--primary-color);margin-left:8px !important}@media screen and (max-width: 800px){.custom-dropdown .dropdown-button span i{font-size:11px;margin-left:5px !important}}.custom-dropdown .dropdown-content{position:absolute;width:100%;max-height:212px;overflow-y:scroll;display:none;position:absolute;background-color:var(--white);box-shadow:0 8px 16px rgba(0,0,0,.2);border-radius:4px;z-index:1;padding:8px;animation:fadeVisibility .5s;transition:.5s ease-in-out}.custom-dropdown .dropdown-content .checkbox-container{margin-bottom:4px;padding:8px;display:flex;align-items:center;width:100%;border-radius:4px;transition:.3s ease-in-out}.custom-dropdown .dropdown-content .checkbox-container .checkbox-label{width:100%;font-size:14px;margin-left:8px;color:var(--primar-text-color)}.custom-dropdown .dropdown-content .checkbox-container.checked{background:var(--primary-variant)}.custom-dropdown .dropdown-content .checkbox-container:hover{background:var(--primary-variant)}.custom-dropdown .dropdown-content .checkbox-container input[type=checkbox]{-moz-appearance:none;-webkit-appearance:none;-o-appearance:none;outline:none;content:none}.custom-dropdown .dropdown-content .checkbox-container input[type=checkbox]:before{font-family:"FontAwesome";content:"";font-size:12px;color:rgba(0,0,0,0) !important;display:block;width:15px;height:15px;border:1px solid #cac7c7;margin-right:7px;border-radius:2px;cursor:pointer;display:flex;align-items:center;justify-content:center}.custom-dropdown .dropdown-content .checkbox-container input[type=checkbox]:checked:before{background:var(--primary-color);color:var(--white) !important}.custom-dropdown .dropdown-content .checkbox-container input[type=checkbox]:checked~.checkbox-label{color:var(--primary-color)}.custom-dropdown .dropdown-content::-webkit-scrollbar{width:20px}.custom-dropdown .dropdown-content::-webkit-scrollbar-thumb{background-color:#e8eaec;border-radius:20px;border:6px solid var(--white)}.custom-dropdown.open .dropdown-content{display:block}